import "../styles/globals.css";
import { useEffect, useState } from "react";
import Layout from "../components/Layout";

export default function App({ Component, pageProps }) {
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    // theme restore (existing behavior)
    const saved = typeof window !== "undefined" && localStorage.getItem("ft_theme");
    if (saved) {
      document.documentElement.classList.toggle("dark", saved === "dark");
    } else {
      const prefersDark = window.matchMedia && window.matchMedia("(prefers-color-scheme: dark)").matches;
      document.documentElement.classList.toggle("dark", prefersDark);
    }

    // enable entrance animation (add class after first paint)
    requestAnimationFrame(() => {
      setMounted(true);
      // tiny delay to allow CSS initial state to apply
      setTimeout(() => {
        const el = document.querySelector(".page-animate");
        if (el) el.classList.add("page-animate-loaded");
      }, 40);
    });
  }, []);

  return (
    <Layout>
      <Component {...pageProps} />
    </Layout>
  );
}
